body {
  background: white;
}
#container {
  background: white;
}
/** contact button and modal */
#contactButton {
  position: absolute;
  right: 25px;
  top: 10px;
  z-index: 40;
  opacity: 1;
  transition: opacity 1s ease-out;
}
.animating_active #contactButton {
  opacity: 0.2;
  cursor: default;
}
#modalContactView {
  background: black;
  color: white;
  height: 0;
  position: absolute;
  right: 50%;
  top: 50%;
  width: 0;
  z-index: 40;
  opacity: 0.2;
  text-align: center;
  transition: width 1s, height 1s, right 1s, top 1s, transform 1s;
  transform: scale(0);
}
.modalView_active #modalContactView {
  height: 100%;
  opacity: 0.9;
  width: 100%;
  right: 0%;
  top: 0%;
  transform: scale(1);
}
.modalView_active .modalContactViewText {
  opacity: 1;
  top: 45%;
  position: absolute;
  text-align: center;
  width: 100%;
}
.modalContactViewText {
  opacity: 0;
}
/*-------------- intro view and main projects navigation */
.introExplainingText {
  padding-top: 15px;
  text-align: center;
  padding-left: 10%;
  padding-right: 10%;
}
.imageLoadingView_active .loadingState {
  opacity: 1;
  transform: scale(1, 1);
}
.loadingState {
  color: #999;
  opacity: 0;
  margin-top: 40px;
  position: relative;
  transition: opacity 0.5s ease-out 0s, transform 0.5s;
  transform: scale(0, 0);
  height: 0px;
}
.imageLoadingView_active .introText,
.intialView_active .introText {
  opacity: 1;
}
.introText__arrow {
  cursor: pointer;
}
.circleBorder {
  border: 1px solid;
  border-radius: 50%;
  padding: 10px;
  margin: 10px;
  margin-top: 10px;
  display: inline-block;
}
#projectListMenu {
  padding-top: 140px;
  transition: transform 1s ease-in-out 0.2s, color 1s ease-in-out 0.2s;
  transform: translateY(0px);
}
.projectListView_active #projectListMenu {
  color: white;
}
.intialView_active #projectListMenu {
  color: black;
}
#ProjectView {
  width: 50%;
  float: left;
}
.projectTitle {
  cursor: pointer;
  height: 8em;
  overflow: hidden;
  padding-bottom: 10px;
  margin-bottom: 0px;
  margin-top: 0px;
  transition: opacity 1s ease-in-out 0.2s, transform 1s ease-in-out 0.2s;
}
.projectListView {
  display: flex;
  transition: transform 2s ease-in-out, opacity 2s, top 2s;
  left: 0px;
  position: absolute;
  top: 0px;
  overflow: hidden;
  transform-origin: top;
  width: 100%;
  height: 100%;
}
#projectList {
  width: 100%;
  position: absolute;
  z-index: 30;
  transition: opacity 1s, transform 2s ease-out;
}
.active.projectTitle {
  transform: scale(1.3) translateX(25%);
}
.arrowSeeProjectDetails {
  cursor: pointer;
  font-size: 20px;
}
.projectTitle .arrowSeeProjectDetails {
  display: none;
  float: right;
}
.active.projectTitle .arrowSeeProjectDetails {
  display: block;
}
#projectList h4 {
  margin: 0px;
  padding-top: 25px;
  padding-bottom: 5px;
}
.projectListView_active #projectList h4,
.projectListView_active #projectList p {
  transition: transform 1s;
  transform: scaleY(0.9);
}
.projectListView_active #projectList .active h4 {
  font-weight: bold;
  transform: scaleY(1);
}
.projectListView_active #projectList .active p {
  transform: scaleY(1);
}
.animating_active .projectTitle {
  opacity: 0.7;
  cursor: default;
}
.animating_active .active.projectTitle {
  opacity: 1;
}
/*---------------- single project details view */
.closeButton {
  margin: 20px;
  color: lightgray;
  opacity: 0;
  cursor: pointer;
  transition: opacity 1s ease-out, transform 1s;
  transform: translateX(-60px);
  z-index: 40;
  position: absolute;
}
.projectDetailsView_active .projectCloseButton,
.modalView_active .modalCloseButton {
  opacity: 1;
  transform: translateX(0px);
}
.projectDetailsIntroView {
  overflow: hidden;
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
  height: 20%;
  opacity: 0;
  transition: transform 2s ease-in-out, opacity 2s, top 2s;
  transform: scale(0, 0);
}
.projectDetailsIntroView h2 {
  font-size: 2em;
  margin-top: 1em;
  margin-bottom: 0.5em;
}
.projectDetailsIntroView p {
  font-size: 1em;
  margin-bottom: 0.5em;
}
.projectDetailsView_active .projectDetailsIntroView {
  opacity: 1;
  transform: scale(1, 1);
}
.projectDetailsMainView {
  overflow: hidden;
  position: absolute;
  transition: transform 2s ease-in-out, opacity 2s, top 2s;
  z-index: 30;
  height: auto;
  transform-origin: bottom;
  width: 100%;
  height: 100%;
  opacity: 0;
  top: 100%;
  transform: scale(0, 0);
}
.projectDetailsView_active .projectDetailsMainView {
  opacity: 1;
  /*top : 70%; */
  transform: scale(1, 1);
}
.projectDetailsContent {
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 40px;
  text-align: center;
}
.projectDetailsContent li {
  text-align: left;
}
.projectDetailsView_active #projectList {
  opacity: 0;
}
/** animations */
.portfolioSlide {
  top: 0%;
  position: absolute;
  width: 100%;
  height: 100%;
  background: white;
}
.portfolioSlideContainer {
  /*position:relative;*/
}
#portfolioProjectAnimationContainer {
  background-color: white;
  position: absolute;
  flex: auto;
  width: 100%;
  border: none;
  right: 0px;
  height: 100%;
  top: 0px;
}
.slideImage {
  height: 100vh;
  min-height: 300px;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
  transition: none;
}
.slideImageOpacityOverlay {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
