.portfolioProjectAnimation-enter {
  position: absolute;
  transition: transform 1s ease-in-out 0.2s;
}
.portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
  opacity: 1;
}
.portfolioProjectAnimation-leave {
  position: absolute;
  opacity: 1;
  transition: transform 1s ease-in-out 0.2s;
}
/*********/
.movingUp .portfolioProjectAnimation-enter {
  top: 100%;
}
.movingUp .portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
