@import "../bower_components/bootstrap/less/bootstrap.less";



@projectListViewAnimations: 1.0s ease-in-out 0.200s;

body {
	background: white;
}

#container {
    background: white;
}

/** contact button and modal */

#contactButton {
	position: absolute;
	right:25px;
	top: 10px;
	z-index:40;
	opacity: 1;
	transition: opacity 1s ease-out;
}

.animating_active #contactButton{
	opacity: 0.2;
	cursor: default;
}

#modalContactView {
	background: black;
	color: white;
    height: 0;
    position: absolute;
    right: 50%;
    top:50%;
    width: 0;
    z-index: 40;
    opacity: 0.2;
    text-align: center;
    transition: width 1s, height 1s, right 1s, top 1s, transform 1s;
    transform: scale(0);
}

.modalView_active #modalContactView {
	height: 100%;
	opacity: 0.9;
	width:100%;
	right: 0%;
    top:0%;
    transform: scale(1);
}


.modalView_active .modalContactViewText {
	opacity: 1;
	top: 45%;
    position: absolute;
    text-align: center;
    width: 100%;
}

.modalContactViewText {
	opacity: 0;
}


/*-------------- intro view and main projects navigation */

.introExplainingText {
	padding-top: 15px;
	text-align: center;
	padding-left: 10%;
    padding-right: 10%;
}

.imageLoadingView_active .loadingState {
    opacity: 1;
    transform : scale(1.0,1.0);
}

.loadingState {
	color: #999;
	opacity: 0;
	
	margin-top: 40px;
    position: relative;
	transition: opacity 0.5s ease-out 0s, transform 0.5s;
    transform : scale(0.0,0.0);
    height: 0px;
}

.imageLoadingView_active .introText, .intialView_active .introText {
	opacity: 1;
}

.introText__arrow {
	cursor: pointer;
}

.circleBorder {
	border: 1px solid;
	border-radius: 50%;
	padding: 10px;
    margin: 10px;
    margin-top: 10px;
    display: inline-block;
}

#projectListMenu
{
    padding-top: 140px;
    transition: transform @projectListViewAnimations, color @projectListViewAnimations;
    transform: translateY(0px);
}

.projectListView_active #projectListMenu {
	color: white;
}

.intialView_active #projectListMenu {
	color: black;
}


#ProjectView {
	width: 50%;
	float: left;
}

.projectTitle {
	cursor: pointer;
	height: 8em;
	overflow: hidden;
    padding-bottom: 10px;
    margin-bottom: 0px;
    margin-top: 0px;
    transition: opacity @projectListViewAnimations, transform @projectListViewAnimations;
}


.projectListView {
	display: flex;
	transition: transform 2s ease-in-out, opacity 2s, top 2s;
	left: 0px;
	position: absolute;
	top: 0px;
	overflow: hidden;
    transform-origin: top;
    width : 100%; 
	height : 100%;
}

.projectListView_active .projectListView {
	
}

#projectList
{
	width: 100%;
    position: absolute;
    z-index: 30;
    transition: opacity 1s, transform 2s ease-out;
}

.active.projectTitle {
	transform: scale(1.3) translateX(25%);
}


.arrowSeeProjectDetails {
    cursor: pointer;
    font-size: 20px;
}
.projectTitle .arrowSeeProjectDetails {
	display: none;
	float: right;
}
.active.projectTitle .arrowSeeProjectDetails {
	display: block;
}

#projectList  h4 {
	margin: 0px;
	padding-top: 25px;
    padding-bottom: 5px;
	
}

.projectListView_active #projectList h4, .projectListView_active #projectList p {
	transition: transform 1s;
	transform: scaleY(0.90);
}

.projectListView_active #projectList .active h4 {
 	font-weight: bold;
 	transform: scaleY(1.0);
}

.projectListView_active #projectList .active p {
	transform: scaleY(1.0);
}


.animating_active .projectTitle {
	opacity: 0.7;
	cursor: default;
}

.animating_active .active.projectTitle {
	opacity: 1;
}


/*---------------- single project details view */


.closeButton {
	margin: 20px;
    color: lightgray;
	opacity: 0;
	cursor: pointer;
	transition: opacity 1s ease-out, transform 1s;
	transform: translateX(-60px);
	z-index: 40;
    position: absolute;
}

.projectDetailsView_active .projectCloseButton, .modalView_active  .modalCloseButton {
	opacity: 1;
	transform: translateX(0px);
}

.projectDetailsIntroView {
	overflow: hidden;
    position: absolute;
    top: 0;
    text-align: center;
    width: 100%;
    height: 20%;
    opacity : 0; 
    transition: transform 2s ease-in-out, opacity 2s, top 2s;
    transform : scale(0.0,0.0);
}

.projectDetailsIntroView h2 {
    font-size: 2em;
    margin-top: 1em;
    margin-bottom: 0.5em;
}

.projectDetailsIntroView p {
	font-size: 1em;
	margin-bottom: 0.5em;
}

.projectDetailsView_active .projectDetailsIntroView {
	opacity : 1; 
	transform : scale(1,1);
}

.projectDetailsMainView {
	overflow: hidden;
	position: absolute;
	transition: transform 2s ease-in-out, opacity 2s, top 2s;
	z-index: 30;
	height: auto;
	transform-origin: bottom;
	width:100%;
	height: 100%;
	opacity : 0; 
	top : 100%; 
	transform : scale(0.0,0.0);
}

.projectDetailsView_active .projectDetailsMainView {
	opacity : 1; 
	/*top : 70%; */
	transform : scale(1,1);
}


.projectDetailsContent {
	padding-left: 5%;
    padding-right: 5%;
    padding-top: 40px;
	text-align: center;
}

.projectDetailsContent li {
	text-align: left;
}

.projectDetailsView_active #projectList {
	opacity: 0;
}


/** animations */


.portfolioSlide {
	top: 0%;
  	position: absolute;
	  width: 100%;
	  height: 100%;
	  background:white;

}

.portfolioSlideContainer {
	/*position:relative;*/
}

#portfolioProjectAnimationContainer {
	background-color: white;
	position: absolute;
	flex: auto;
	width: 100%;
	border: none;
	right: 0px;
	height: 100%;
	top: 0px;
}


.slideImage {
	height: 100vh;
    min-height: 300px;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    display: flex;
    align-items: flex-end;
    transition: none;
}


.slideImageOpacityOverlay { 
	opacity: 0;
	transition: opacity 1s ease-in-out;
	width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: black;
}

.projectListView_active .slideImageOpacityOverlay, .projectDetailsView_active .slideImageOpacityOverlay {
    opacity: 0.5;
}

.pointer {
	cursor: pointer;
}

.arrow__individualProjecCarousel {
    cursor: pointer;
    opacity: 0;
    position: absolute;
    transition: opacity 1s ease-in-out;
}

.projectDetailsView_active .arrow__individualProjecCarousel {
	opacity: 1;
}

.projectDetailsView_active.singleImageProject .arrow__individualProjecCarousel {
	opacity: 0;
}

.projectDetailsView_active .animating_active .arrow__individualProjecCarousel {
	opacity: 0.1;
}

#leftArrow__individualProjecCarousel {
    left: 10%;
    top: 15%;
}

#rightArrow__individualProjecCarousel {
    right: 10%;
    top: 15%;
}

.portfolioProjectAnimation-enter {
	position: absolute;
	transition: transform @projectListViewAnimations;
}

.portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
  opacity: 1;
}

.portfolioProjectAnimation-leave {
	position: absolute;
	opacity: 1;
	transition: transform @projectListViewAnimations;
}

.portfolioProjectAnimation-leave.portfolioProjectAnimation-leave-active {
}

/*********/

.movingUp .portfolioProjectAnimation-enter {
	top:100%;
}

.movingUp .portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
  transform: translateY(-100%);
}

.movingUp .portfolioProjectAnimation-leave {
}

.movingUp .portfolioProjectAnimation-leave.portfolioProjectAnimation-leave-active {
  	transform: translateY(-100%);
}


/*********/

.movingDown .portfolioProjectAnimation-enter {
  top:-100%;
}

.movingDown .portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
  transform: translateY(+100%);
}

.movingDown .portfolioProjectAnimation-leave {
}

.movingDown .portfolioProjectAnimation-leave.portfolioProjectAnimation-leave-active {
 	transform: translateY(+100%);
}

/*********/

.movingLeft .portfolioProjectAnimation-enter {
	top:0%;	
	left:100%;
}

.movingLeft .portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
  transform: translateX(-100%);
}

.movingLeft .portfolioProjectAnimation-leave {
	top:0%;
	left:0%;
}

.movingLeft .portfolioProjectAnimation-leave.portfolioProjectAnimation-leave-active {
 	transform: translateX(-100%);
}

/*********/

.movingRight .portfolioProjectAnimation-enter {
	top:0%;	
  right:100%;
}

.movingRight .portfolioProjectAnimation-enter.portfolioProjectAnimation-enter-active {
  transform: translateX(100%);
}

.movingRight .portfolioProjectAnimation-leave {
	top:0%;
}

.movingRight .portfolioProjectAnimation-leave.portfolioProjectAnimation-leave-active {
 	transform: translateX(100%);
}


/* Extra small devices (phones, less than 768px) */
/* No media query since this is the default in Bootstrap */

	body {
		font-size: 25px;
	}

	.introExplainingText {
	    font-size: 2em;
	    text-align: left;
	}

	#projectListMenu {
		margin-left: 5%;
		width: 85%;
	}

	h4, .h4 {
	    font-size: 4rem;
	}

	.active.projectTitle {
	    transform: translateX(8%);
	}

	.projectTitle .arrowSeeProjectDetails {
		font-size: 1em;
	    margin-right: 100px;
	}



	.projectDetailsView_active .projectListView {
	    transform: scale(1.0, 0.6)  translateY(30%);
	}

	.arrow__individualProjecCarousel {
	    color: lightgrey;
	    font-size: 10em;
	    z-index: 20;
	}

	.projectDetailsView_active .closeButton {
    font-size: 6rem;
	}


	.projectDetailsIntroView {
		padding-left: 10%;
    	padding-right: 10%;
	}


 	.projectDetailsView_active .projectDetailsMainView {
 		top: 80%;
 	}




/* Small devices (tablets, 768px and up) */
@media (min-width: @screen-sm-min) { 

	.introExplainingText {
		font-size: 1.2em;
	}

	h4, .h4 {
	    font-size: 4rem;
	}

	.projectDetailsView_active .projectListView {
	    transform: scale(1.0, 0.6)  translateY(16em);
	}
}

/* Small devices with retina display (tablets, 768px and up) */
@media (max-width: @screen-md-min) and (min-resolution: 2dppx) {
	h4, .h4 {
	    font-size: 8rem;
	}

	#contactButton {
	    font-size: 4rem;
	}
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: @screen-md-min) { 

	body {
		font-size: 14px;
	}

	.introExplainingText {
		font-size: 1em;
		 text-align: center;
	}

	#projectListMenu {
		margin: auto;
		width: 450px;
	}

	h4, .h4 {
	    font-size: 2rem;
	}

	.active.projectTitle h4, .active.projectTitle p {
		/*font-size: 140%;*/
	}

	.projectDetailsView_active .projectListView {
	    transform: scale(0.7, 0.6) translateY(12em);
	}

	.arrow__individualProjecCarousel {
	    color: black;
	    font-size: 5em;
	}

	.projectDetailsView_active .closeButton {
	    font-size: 3rem;
	}

	/*.projectDetailsView_active .projectDetailsMainView {
 		top: 70%;
 	}*/

}
